/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { useRecoilValue } from 'recoil'
import { uniq } from 'lodash'
import Skeleton from '@mui/material/Skeleton'

import { selectReports } from '../../data/selectors'
import { DataGrid, renderers } from '../DataGrid'
import { withSuspense } from '../withSuspense'
import { reportsColumnDefs } from './columnDefs'
import { Title } from '../Title'
import { fetchReportDataByReportId } from '../../data/calls'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { createSingleTableWorkbook } from '../../lib/excel/createSingleTableWorkbook'
import { downloadWorkbook, downloadWorkbookAsCsv } from '../../lib/excel/downloadWorkbook'

export const ReportsListing = withSuspense(
  () => {
    const api = useApi()
    const notifications = useNotifications()

    const reports = useRecoilValue(selectReports)

    const rows = useMemo(() => {
      return reports.map(({ id, name, description }) => {
        const createWorkbook = async () => {
          try {
            const cancelNotification = notifications.info('Still creating report...', 2500)
            const data = await fetchReportDataByReportId(api)(id)
            cancelNotification()
            const columnNames = data.reduce<string[]>((acc, reportRow) => uniq([...acc, ...Object.keys(reportRow)]), [])
            return createSingleTableWorkbook({
              wsName: name,
              columns: columnNames.map((columnName) => ({
                key: columnName,
                header: columnName,
              })),
              rows: data,
            })
          } catch (err) {
            notifications.error(`Failed to create report: ${err}`)
          }
        }

        return {
          id,
          name,
          description,
          downloadXlsx: {
            label: 'Download XLSX',
            onClick: async () => {
              const wb = await createWorkbook()
              if (!wb) return
              downloadWorkbook(wb, `Report ${name}.xlsx`)
            },
          },
          downloadCsv: {
            label: 'Download CSV',
            onClick: async () => {
              const wb = await createWorkbook()
              if (!wb) return
              downloadWorkbookAsCsv(wb, `Report ${name}.csv`)
            },
          },
        }
      })
    }, [reports])

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <Title>Reports</Title>
        <div css={{ flex: 1 }}>
          <DataGrid rows={rows} columnDefs={reportsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <Skeleton variant="rectangular" height="20em" />
)
